import React from 'react';
import { useEffect, useState, useRef } from 'react';
import {
  Button,
  Form,
  Container,
  FloatingLabel,
  Alert,
  Row,
  Col,
  Modal,
  Navbar,
} from 'react-bootstrap';
import style from './Record.module.css';
import CameraComponent from './Camera';
import ExerciseEndModal from './ExerciseEndModal';

import { useLocation } from 'react-router-dom';

function Record() {
  // ExerciseStartModal  
  const location = useLocation();
  const { exerciseType, exerciseGoal, exerciseDirection, exerciseMass } =
    location.state;

  // count  
  const [count, setCount] = useState(0);
  // count 1 
  const increaseCount = () => {
    setCount(prevCount => prevCount + 1);
  };

  const [time, setTime] = useState(0);

  // camera     
  const [hasPermission, setHasPermission] = useState(null);
  const startTime = useRef(null);
  useEffect(() => {
    startTime.current = new Date();
  }, []);

  // hasPermission true  1 time 1 
  useEffect(() => {
    if (hasPermission) {
      // startTime   

      const interval = setInterval(() => {
        setTime(prevTime => prevTime + 1);
      }, 1000);
      return () => clearInterval(interval);
    }
  }, [hasPermission]);

  //    state
  //   ,    true 
  const [isExerciseEnd, setIsExerciseEnd] = useState(false);
  //   Modal
  const [showModal, setShowModal] = useState(false);
  const handleModalOpen = () => {
    setShowModal(true);
  };

  return (
    <div>
      <Container>
        <Navbar>
          <Container>
            <Navbar.Brand>SHrack -   </Navbar.Brand>
          </Container>
        </Navbar>
        <Row className="justify-content-center">
          <div className={style.item_camera}>
            <CameraComponent
              count={count}
              setCount={setCount}
              hasPermission={hasPermission}
              setHasPermission={setHasPermission}
              exerciseType={exerciseType}
              exerciseGoal={exerciseGoal}
              exerciseDirection={exerciseDirection}
              exerciseMass={exerciseMass}
              isExerciseEnd={isExerciseEnd}
            />
          </div>
        </Row>

        <div className={style.item}>
          <h1>{count}</h1>
          <div>  {time}</div>
          <Button onClick={handleModalOpen}></Button>
        </div>
      </Container>
      <ExerciseEndModal
        isModalShow={showModal}
        setModalShow={setShowModal}
        count={count}
        exerciseType={exerciseType}
        exerciseGoal={exerciseGoal}
        exerciseMass={exerciseMass}
        startTime={startTime}
      />
    </div>
  );
}

export default Record;
